  components: { Dialog },
  data() {
    return {
      tab,
      info,
      base,
      detail,
      btnName: "Info",
      isBase: true,
    };
  },
  computed: {
    getInfo() {
      return JSON.parse(JSON.stringify(info));
    },
    getBaseData() {
      const baseData = JSON.parse(JSON.stringify(base));
      return baseData[0]["data"];
    },
    getDetailData() {
      const detailData = JSON.parse(JSON.stringify(detail));
      return detailData[0]["data"];
    },
  },
  methods: {
    checkTab(t) {
      if (t !== "Base Information") {
        this.isBase = false;
      } else {
        this.isBase = true;
      }
    },
  },
};
</script>

