<template>
  <v-card>
      <v-col cols="5">
        <v-treeview
          :active.sync="active"
          :items="lists"
          :load-children="getChildren"
          :open.sync="open"
          item-key="name"
          activatable
          color="warning"
          open-on-click
          transition
        >
        </v-treeview>
      </v-col>
  </v-card>
</template>

<script>
  const pause = ms => new Promise(resolve => setTimeout(resolve, ms))
  import { MainGroup, SubGroup } from "../data/tree";

  export default {
    mounted(){
      this.lists = this.getRootItems
    },
    data: () => ({
      MainGroupItems:MainGroup["items"],
      SubGroupItems:SubGroup["items"],
      lists:[],
      active: [],
      open: [],
      users: [],
    }),

    computed: {
      getRootItems(){
          let newArr = []
          let items = this.MainGroupItems
          let result = []
          for(let i=0; i < items.length; i++) {
              if(!newArr.includes(items[i][0])){
                let rootObj = {name:'', children:[]}
                newArr.push(items[i][0])
                rootObj.name = items[i][0]
                result.push(rootObj)
              } 
          }
          return result
      },
      getLevelOneArr() {
        let result = []
        let items = this.MainGroupItems
        for(let i=0; i < items.length; i++){
          result.push(items[i][1])
        }
        return result
      },
       getLevelTwoArr() {
        let result = []
        let items = this.SubGroupItems
        for(let i=0; i < items.length; i++){
          if(items[i][2]==2){
            result.push(items[i][0])
          }
        }
        return result
      }
    },
    methods: {
      async getChildren (item) {
        await pause(300)
        if(this.getLevelOneArr.some(el => el === item.name)){
          let items = this.SubGroupItems
          for(let i=0; i < items.length; i++){
            let levelTwoObj = {name:'', children:[]}
            levelTwoObj.name = items[i][0]
            if(item.name === items[i][1]){
              item.children.push(levelTwoObj)
            }
          }
        }else if(this.getLevelTwoArr.some(el => el === item.name)) {
          let items = this.SubGroupItems
          for(let i=0; i < items.length; i++){
              let levelThreeObj = {name:'', children:[]}
              levelThreeObj.name = items[i][0]
              if(item.name === items[i][1]){
                item.children.push(levelThreeObj)
              }
          }
        } else {
          let items = this.MainGroupItems
          for(let i=0; i < items.length; i++) {
            let levelOneObj = {name:'', children:[]}
            levelOneObj.name =items[i][1]
            if(item.name === items[i][0]){
              item.children.push(levelOneObj)
            }
          }
        }
      }
    },
  }
</script>

